export default {
	data() {
		return {
			details: '',
			loadEnd: false
		}
	},
	onLoad(options) {
		this.loadData(options.id)
	},
	methods: {
		loadData(id) {
			const that = this;
			uni.request({
				url: "http://www.codestock.club/tang/api/poem/getPoemById/" + id,
				timeout: 5000,
				success(res) {
					that.details = res.data;
					that.loadEnd = true;
				},
				fail(err) {
					console.log(err)
				}
			})
		},
		find(name) {
			uni.reLaunch({
				url: '/pages/shi/index?name=' + name
			});
		}
	}
}
